<template>


    <div class="box" :style="{ 'aspect-ratio': initRatio }">
        <div v-if="initRatio" class="text-wrap">{{ initRatio }}</div>

        <span class="ratio-1-1"></span>
        <span class="ratio-2-1"></span>
        <span class="ratio-3-2"></span>
        <span class="ratio-4-3"></span>
        <span class="ratio-5-4"></span>
        <span class="ratio-16-9"></span>
        <span class="ratio-16-10"></span>
    </div>

</template>

<script>
import { toRef } from 'vue'

export default {
    name: 'RatioAspectGraph',
    props: ['ratio'],

    setup(props) {
        // props
        let initRatio = toRef(props, 'ratio')

        return {
            initRatio,
        }
    }
}
</script>